<template>
  <div class="hint">
		<input v-if="this.symbol == 'symbol-A'" type="button" value="A" class="symbol" />
		<input v-else-if="this.symbol == 'symbol-V'" type="button" value="V" class="symbol" />
		<input v-else-if="this.symbol == 'symbol-I'" type="button" value="I" class="symbol" />
		<input v-else type="button" value="S" class="symbol" />

		<span class="message" ><font size="2">{{ this.message }}</font></span>
  </div>
</template>

<script>

export default {
	name: 'Hint',
	props: {
		symbol: String,
		message: String
	}
}
</script>

<style scoped>
	.symbol {
		height: 26px;
		width: 26px;
		background-color: transparent;
		border-radius: 50%;
		border: 1px solid white;
		color: white;
		margin-left: 5px;
		margin-top: 10px;
		margin-right: 5px;
	}

	.message {
		border: none;
		background-color: transparent;
		color: white;
		margin-left: 5px;
	}

	.message:hover {
		color: rgb(226, 144, 36);
	}

	.message:focus {
		color: rgb(226, 144, 36);
	}

	.hint {
		display: flex;
    	border-top: 1px solid #777;
		padding: 7px 15px 5px 0px;
		cursor: pointer;
	}

	.message font:active {
		color: rgb(226, 144, 36);
	}
</style>
