	getters: {
		getSelectedCategory: state => {
			return state.selectedCategory
		},
		getSearchedHint: state => {
			return state.searchedHint
		},
		getCurrentHint: state => {
			return state.currentHint;
		},
		getSelectedMenu: state => {
			return state.selectedMenu
		},
        getFilteredHints: state => {
			return state.filteredHints
		}
	},
	mutations: {
		ADD_CATEGORY: (state, category) => {
			state.selectedCategory.push(category)
		},
		REMOVE_CATEGORY: (state, category) => {
			state.selectedCategory.splice(state.selectedCategory.indexOf(category), 1)
		},
		ADD_SEARCHEDHINT: (state, category) => {
			state.data.filter(element => {
				if (element && element.type.toUpperCase().trim() === category.toUpperCase().trim()) {
					state.searchedHint.push(element)
				}
			})
		},
		REMOVE_SEARCHEDHINT: (state, category) => {
			state.searchedHint = state.searchedHint.filter(element => {
				return element.type.toUpperCase().trim() !== category.toUpperCase().trim()
			})
		},
		SET_CURRENTHINT: (state, index) => {
			state.currentHint = state.searchedHint[index]
		},
        SET_MENU: (state, selectedMenu) => {
            state.selectedMenu = selectedMenu
            state.searchedHint = []
            state.selectedCategory = []
			state.currentHint = []
        },
        FILTER_HINTS: (state, filteredHints) => {
            state.filteredHints = []


            state.data.filter(element => {
                if (element && element.type.toUpperCase().trim() === filteredHints.toUpperCase().trim()) {
                    state.filteredHints.push(element)
                }
            })
		},
		FILTER_CLEAR: (state) => {
			state.filteredHints = []
		}
	},
	actions: {
		addCategory: (context, category) => {
			context.commit("ADD_CATEGORY", category)
		},
		removeCategory: (context, category) => {
			context.commit("REMOVE_CATEGORY", category)
		},
		addSearchedHint: (context, category) => {
			context.commit("ADD_SEARCHEDHINT", category)
		},
		removeSearchedHint: (context, category) => {
			context.commit("REMOVE_SEARCHEDHINT", category)
		},
		setCurrentHint: (context, index) => {
			context.commit("SET_CURRENTHINT", index)
		},
		selectMenu: (context, selectedMenu) => {
			context.commit("SET_MENU", selectedMenu)
		},
		filterHint: (context, filteredHints) => {
            context.commit("FILTER_HINTS", filteredHints)
		},
        filterClear: (context) => {
			context.commit("FILTER_CLEAR")
		}
	}
