<script>
import { mapActions } from 'vuex'
import CollapseButton from './CollapseButton.vue'

export default {
	name: "CollapseMenu",
	components: {
		CollapseButton
	},
    mounted: function () {
        this.selectMenu('Menu 1')
    },
	methods: {
		...mapActions([
			'addCategory',
			'removeCategory',
			'addSearchedHint',
			'removeSearchedHint',
			'selectMenu'
		]),
		handleCategory: function(e) {
			var category = e.target.nextSibling.innerHTML;
			e.target.checked ? [
				this.addCategory(category),
				this.addSearchedHint(category)
			] : [
				this.removeCategory(category),
				this.removeSearchedHint(category)
			]

		},
		menuSelect: function (param) {
		    this.selectMenu(param)
			this.checkedCategories = []
		}
	},
	data() {
		return {
			selected: [],
			checkedCategories: []
		}
	}
}
</script>

<style scoped>
