<template>
	<div class="info">

			

			<div class="header__nav">
				<div class="hints__list">
					<p class="list">{{ getSelectedMenu }} <span>/</span></p>
					<p class="list" v-for='(category, index) in getSelectedCategory' v-bind:class="{'active': nav == category}" v-on:click="hintsFilter(category)" v-bind:key='index'>
						{{ category }}
						<span>/</span>
					</p>
					<p class="clear__filter" v-on:click="clearFilter" v-if="getFilteredHints.length">(Clear Filter)</p>
				</div>

				<!-- Screen Size Control Button -->
				<div class="sizing__buttons">
					<input type="button" value="N" id="btn-2x" v-on:click="handleScreenSize('normal')" />
					<input type="button" value="TH" id="btn-2x" v-on:click="handleScreenSize('2xf')" />
					<input type="button" value="FULL" id="btn-full" v-on:click="handleScreenSize('fullf')" />
					<input type="button" value="X" id="btnFullScreenClose" v-on:click="handleFullScreenClose" />
				</div>
			</div>

		<font size="5" color="white">{{ getCurrentHint.title }}</font>

		<!-- Carousel -->
		<div class="carousel">
			<b-carousel
				id="carousel1"
				style="text-shadow: 1px 1px 2px #333;"
				controls
				fade
				background="#ababab"
				:interval="4000"
				img-width="1024"
				img-height="592"
				v-model="slide"
				@sliding-start="onSlideStart"
				@sliding-end="onSlideEnd"
			>
				<b-carousel-slide
					v-for='(slide, index) in getCurrentHint.slides'
					:key='index'
					:img-src=slide
					:text=getCurrentHint.description[index]
				/>
			</b-carousel>
		</div>
</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'InfoPanel',
  computed: {
		...mapGetters([
			'getSelectedCategory',
			'getCurrentHint',
			'getSelectedMenu',
            'getFilteredHints'
		]),
	},
	data() {
		return {
			slide: 0,
			sliding: null,
			flagMenu: '1xt',
			nav: ''
		}
	},
	methods: {
        ...mapActions([
            'filterHint',
            'filterClear'
        ]),
		onSlideStart() {
			this.sliding = true
		},
		onSlideEnd() {
			this.sliding = false
		},
		handleScreenSize: function(size) {
			if (size === '2xt' || size === '2xf') {
				if (this.flagMenu === '2xt' || this.flagMenu === '2xf') return

				document.getElementById("collapse-menu-content").style.display = "none"
				document.getElementById("collapsemenu").style.display = "block"
				if (this.flagMenu === '1xt' || this.flagMenu === 'fullt') {
					document.getElementById("collapsebutton").click()
				}
				document.getElementById("freesearch-panel").style.display = "none"
				document.getElementById("message-panel").style.display = "none"
				var obj1 = document.getElementById("info-panel")
				obj1.style.marginLeft = "10%"
				obj1.style.marginTop = "2.5%"
				obj1.style.width = "80%"
				obj1.style.height = "80%"
				document.getElementById("txtTitle").style.display = "none"
				document.getElementById("navCategory").style.display = "block"
				this.flagMenu = '2xf';
			} else {
				if (this.flagMenu === 'fullt' || this.flagMenu === 'fullf') return

				document.getElementById("collapsemenu").style.display = "none"
				document.getElementById("collapse-menu-content").style.display = "none"
				document.getElementById("freesearch-panel").style.display = "none"
				document.getElementById("message-panel").style.display = "none"
				var obj2 = document.getElementById("info-panel")
				obj2.style.marginLeft = "0%"
				obj2.style.marginTop = "0%"
				obj2.style.width = "100%"
				obj2.style.height = "100%"
				document.getElementById("txtTitle").style.display = "none"
				document.getElementById("navCategory").style.display = "none"
				document.getElementById("collapsemenu").style.display = "none"
				// obj2 = document.getElementById("btnFullScreenClose")
				// obj2.style.left = "100px"
				// obj2.style.top = "100px"
				// obj2.style.zIndex = 2
				if (this.flagMenu === '1xt' || this.flagMenu === '2xt'){
					this.flagMenu = 'fullt'
				} else {
					this.flagMenu = 'fullf'
				}
			}
		},
		handleFullScreenClose() {
            document.getElementById("collapsemenu").style.display = "block"
            document.getElementById("collapse-menu-content").style.display = "block"
            document.getElementById("freesearch-panel").style.display = "block"
            document.getElementById("message-panel").style.display = "block"
            var obj2 = document.getElementById("info-panel")
            obj2.style.marginLeft = "0%"
            obj2.style.marginTop = "0%"
            obj2.style.width = "1024px"
            obj2.style.height = "500px"
            this.flagMenu = '1xt'
        },
		hintsFilter: function (param) {
            this.filterHint(param)
            this.nav = param;
		},
        clearFilter: function () {
            this.filterClear()
			this.nav = '';
		}
	}
}

</script>

<style scoped>
.info {
	margin: 3% 2% 20% 2%;
}

.carousel {
	margin-top: 20px;
}

#btn-2x {
	position: relative;
	z-index: 1;
	background-color: transparent;
	/* outline: none; */
	border: none;
	color: white;
}

#btn-full {
	position: relative;
	z-index: 1;
	background-color: transparent;
	border: none;
	color: white;
	margin-right: 10px;
}

#btnFullScreenClose {
	position: relative;
	background-color: rgb(226, 144, 36);
	border-color: transparent;
	color: #fff;
	height: 21px;
	line-height: 9px;
	z-index: 10;
}


.header__nav {
	width: 100%;
	margin-top: 1px;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.hints__list {
	size: 4.5;
	color: #fff;
	display: flex;
	align-items: center;
}

	.list {
		float: left;
		margin-right: 5px;
		margin-left: 3px;
		cursor: pointer;
		margin-bottom: 0;
	}

	.list.active {
		color: rgb(226, 144, 36);
	}

	span {
		color: #fff !important;
		margin-left: 5px;
	}

	.clear__filter {
		font-size: 10px;
		padding-top: 0px;
		margin-left: 5px;
		margin-bottom: 12px;
		float: left;
		cursor: pointer;
	}

	.clear__filter:hover {
		color: rgb(226, 144, 36);
	}

	.sizing__buttons {
		width: auto;
		box-sizing: border-box;
		padding: 5px;
		float: right;
	}

</style>
