<template>
	<input type="button" v-b-toggle="collapseid" class="collapsebutton" value="X" v-on:click="changeBackcolor" />
</template>

<script>
export default {
	name: 'CollapseButton',
	props: {
		collapseid: String
	},
	methods: {
		changeBackcolor(e) {
			var color = e.target.style.backgroundColor
			e.target.style.backgroundColor = (color != "rgb(226, 144, 36)") ? "rgb(226, 144, 36)" : "transparent"
		}
	}
}
</script>

<style scoped>
	.collapsebutton {
		background-color: transparent;
		border: 1px solid;
		border-color: #777777;
		color: #777777;
		padding: 0 0px 0 0px;
		outline: none;
		height: 21px;
		line-height: 9px;
		width: 16px;
	}
</style>
