<template>
	<div>
		<div class="condition-panel">
			<form>
				<!-- Free Search -->
				<b-form-input type="text" size="sm" placeholder="Free Search" class="condition-panel-element" />

				<!-- Select Format -->
				<b-form-input list="format-list-id" size="sm" placeholder="Size Format" :select-size="2" class="condition-panel-element" />
				<datalist id="format-list-id">
						<option v-for="(format, index) in formats" v-bind:key="index">{{ format }}</option>
				</datalist>
				
				<!-- Size Options -->
				<!-- <b-form-input list="size-list-id" size="sm" placeholder="Size Options" class="condition-panel-element" />
				<datalist id="size-list-id">
						<option v-for="(size, index) in sizes" v-bind:key="index">{{ size }}</option>
				</datalist> -->
			</form>
		</div>

		<div class="relevant-hints">
			<CollapseButton collapseid="collapse-hint-content" class="mark" /> <font color="#f8f9fa" size="2"> RELEVANT HITS </font>
			<b-collapse visible id="collapse-hint-content" class="hint__content">

				<div v-for='(hint, index) in getFilteredHints' v-if="getFilteredHints.length" v-bind:key='index' v-on:click="handleSetCurrentHint(index)">
					<Hint v-if="hint.type.toUpperCase() == 'AUDIO'" symbol="symbol-A" message="Main heading over mostly use one line It is the same as the heading" />
					<Hint v-else-if="hint.type.toUpperCase() == 'VIDEO'" symbol="symbol-V" message="Main heading over mostly use one line It is the same as the heading" />
					<Hint v-else symbol="symbol-S" message="Main heading over mostly use one line It is the same as the heading" />
				</div>

				<div v-for='(hint, index) in getSearchedHint' v-if="getFilteredHints.length == 0" v-bind:key='index' v-on:click="handleSetCurrentHint(index)">
					<Hint v-if="hint.type.toUpperCase() == 'AUDIO'" symbol="symbol-A" message="Main heading over mostly use one line It is the same as the heading" />
					<Hint v-else-if="hint.type.toUpperCase() == 'VIDEO'" symbol="symbol-V" message="Main heading over mostly use one line It is the same as the heading" />
					<Hint v-else symbol="symbol-S" message="Main heading over mostly use one line It is the same as the heading" />
				</div>
			</b-collapse>
		</div>
	</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import CollapseButton from './CollapseButton.vue'
import Hint from './Hint.vue'

export default {
  	name: 'FreeSearchPanel',
	components: {
		CollapseButton,
		Hint
	},
	computed: {
		...mapGetters([
			'getSearchedHint',
			'getFilteredHints'
		]),
	},
	methods: {
		...mapActions([
			'setCurrentHint'
		]),
		handleSetCurrentHint: function(index) {
			this.setCurrentHint(index)
		}
	},
	data() {
		return {
			formats: ['Size', 'Video', 'Audio', 'Slide', 'Image'],
			sizes: ['Small', 'Medium', 'Large', 'Extra Large']
		}
	}
}
</script>

<style scoped>
	.condition-panel {
		padding: 10% 2.2% 2.4% 2.2%;
		width: 100%;
	}

	.condition-panel-element {
		margin-bottom: 2%;
		background-color: transparent;
		color: white;
	}

	.condition-panel-element:focus {
		background: transparent;
		outline: none !important;
		color: white;
	}

	.mark	{
		margin: 5% 2% 5% 2%;
		width: 16px;
	}

	.relevant-hints {
		font: white;
		overflow: auto;
	}

	.hint__content {
		height: 270px;
		overflow-y: auto;
	}

	.message:active {
		color: rgb(226, 144, 36);
	}

	.hint__content::-webkit-scrollbar-track
	{
		-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
		border-radius: 10px;
		/*background-color: #F5F5F5;*/
	}

	.hint__content::-webkit-scrollbar
	{
		width: 6px;
		/*background-color: #F5F5F5;*/
	}

	.hint__content::-webkit-scrollbar-thumb
	{
		border-radius: 3px;
		-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
		background-color: #555;
	}

</style>
